@keyframes slideInLeft {
  0% {
    transform: translate3d(-250px, 0, 0);
    visibility: visible; }
  100% {
    transform: translate3d(0, 0, 0); } }

@keyframes slideOutLeft {
  0% {
    transform: translate3d(0, 0, 0); }
  100% {
    transform: translate3d(-250px, 0, 0);
    visibility: hidden; } }

html,
body.open {
  height: 100%;
  margin: 0;
  min-height: 100%; }

body {
  font-family: helvetica-neue, helvetica;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: relative; }
  body:after {
    background: #2196f3;
    content: '';
    height: 100%;
    left: 0;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    top: 0;
    transition: opacity 0.4s;
    visibility: hidden;
    width: 100vw;
    z-index: 10; }
  body.open:after {
    background: #2196f3;
    height: 100%;
    opacity: 1;
    visibility: visible; }

.b-nav {
  position: absolute;
  z-index: 11; }
  .b-nav:not(.open) {
    visibility: hidden; }
  .b-nav li {
    color: white;
    list-style-type: none;
    padding: 10px;
    text-align: left;
    transform: translateX(-250px); }
  .b-nav li:not(.open) {
    animation-duration: 0.4s;
    animation-fill-mode: both;
    animation-name: slideOutLeft; }
  .b-nav li:first-child {
    padding-top: 132px; }
  .b-nav.open li {
    animation-duration: 0.4s;
    animation-fill-mode: both;
    animation-name: slideInLeft; }

.b-link {
  border-left: #fff solid 0;
  color: #fff;
  font-size: 24px;
  margin-left: 50px;
  text-decoration: none;
  transition: all 0.4s ease;
  width: auto; }

.b-link:hover,
.b-link--active {
  border-left: #fff solid 2px;
  padding-left: 30px; }

.b-menu {
  background: #2196f3;
  border: #fff solid 2px;
  border-radius: 50%;
  cursor: pointer;
  display: inline-block;
  height: 52px;
  padding-left: 12px;
  padding-top: 14px;
  position: relative;
  transition: all 0.4s ease;
  width: 52px;
  z-index: 12; }

.b-bun {
  background: #fff;
  position: relative;
  transition: all 0.4s ease; }

.b-bun--top {
  height: 2px;
  top: 0;
  width: 24px; }

.b-bun--mid {
  height: 2px;
  top: 8px;
  width: 24px; }

.b-bun--bottom {
  height: 2px;
  top: 16px;
  width: 24px; }

.b-brand {
  color: #2196f3;
  font-size: 24px;
  left: 0;
  margin-left: 30px;
  position: relative;
  text-decoration: none;
  top: -17.4285714286px;
  transition: all 0.4s ease;
  z-index: 2000; }

.b-container {
  left: 50px;
  position: absolute;
  top: 50px;
  width: auto; }
  .b-container:hover:not(.open) .b-menu {
    background: #51adf6; }
  .b-container.open .b-main {
    border: #fff solid 2px; }
  .b-container.open .b-bun--top {
    background: #fff;
    top: 9px;
    transform: rotate(45deg); }
  .b-container.open .b-bun--mid {
    opacity: 0; }
  .b-container.open .b-bun--bottom {
    background: #fff;
    top: 5px;
    transform: rotate(-45deg); }
  .b-container.open .b-brand {
    color: #fff; }
  .b-container.open:hover .b-menu {
    border: #fff solid 2px; }
